body, html {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  height: 100%;
}

.contain {
  height: 300vh; /* Ensures enough space for all three images to scroll */
}

.image-contain {
  position: sticky;
  top: 0;
  width: 100%;
  height: 100vh; /* Ensures the image occupies the full viewport */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  flex-direction: column; /* Ensures that the image and text are stacked vertically */
  background-color: black;
}

.overlay {
  color: white;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  z-index: 10; /* Ensures the text is on top of the image */
}

.svg-icon {
  height: 250px;
  margin-bottom: 20px;
  object-fit: contain;
}

.overlay h1.caption {
  font-size: 48px;
  margin: 0;
}

.overlay p {
  font-size: 18px;
  margin: 10px 0 0;
}

.image-1 {
  z-index: 1;
}

.image-2 {
  z-index: 2;
}

.image-3 {
  z-index: 3;
}

@media screen and (max-width: 600px) {
  .svg-icon {
    height: 170px;
  }

  .overlay h1.caption {
    font-size: 32px;
  }

  .overlay p {
    font-size: 14px;
  }
}
