/*Changes*/
h1{
    padding-top: 80px;
}

.custom-dropdown {
    position: relative;
    margin: 20px;
    display: inline-block;
}

.dropdown-select {
    background-color: #000;
    color: #fff;
    padding: 10px 40px;
    border: 2px solid;
    border-image: linear-gradient(to right, rgb(31, 175, 31), rgb(51, 154, 182)) 1;
    border-radius: 0px;
    font-size: 20px;
    cursor: pointer;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    outline: none;
}

.custom-dropdown:after {
    content: '▼';
    font-size: 12px;
    color: #fff;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    pointer-events: none;
}

.dropdown-select:hover {
    background-color: #222;
}

.dropdown-select:focus {
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.component-container{
    margin-bottom: 30px;
}

.cards-head{
    padding-top: 50px;
    border-bottom: 3px solid white;
    width: fit-content;
    border-image: linear-gradient(to right, rgb(31, 175, 31), rgb(51, 154, 182)) 1;
    margin-bottom: 15px;
}

@media screen and (max-width: 600px) {

.dropdown-select {
    padding: 5px 20px;
}
.dropdown-select {
    font-size: 15px;
}

.container .box-container .box {
    width: 270px;
    animation: CSSscrollReveal ease-in-out both;
    animation-timeline: view();
    animation-range: entry 50% cover 50%;
}

@keyframes CSSscrollReveal {
    from{
        opacity: 0;
        transform: translateY(100px);
    }
    to{
        opacity: 1;
        transform: tramslateY(0px);
    }
    
}
}
